.contact {
  width: 100%;
  height: 90vh;
  display: flex;
}

.contact  img{
  width: 90%;
  height: 100%;
}
.contact .leftSide {
  height: 100%;
  flex: 50%;

}

.contact .rightSide {
  height: 100%;
  flex: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contact .rightSide h3 {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-weight: 10;
  font-size: 30px;
  margin-left: 30px;
}

form {
  display: flex;
  flex-direction: column;
  width: auto;
  height: auto;
  padding: 30px;
}

form input {
  height: 40px;
  width: 80%;
  border: none;
  border-bottom: 1px solid #121619;
  color: black;
}

form textarea {
  margin-top: 15px;
  height: 70px;
  width: 80%;
  border: none;
  border-bottom: 1px solid #121619;
  color: black;
}
input::placeholder,
textarea::placeholder {
  font-weight: bold;
  color: black;
  font-family: Arial, Helvetica, sans-serif;
}
form input:focus,
textarea:focus {
  outline: none;
}

form label {
  margin-top: 15px;
  color: grey;
}

form button {
  margin-top: 40px;
  width: 80%;
  height: 50px;
  border: none;
  background-color: #085E7D;
  color: whitesmoke;
  font-size: 25px;
  cursor: pointer;
}


@media only screen and (max-width: 800px) {
  .contact  img {
    width: 90%;
    height: 85%;
    
  }
}